  font-size: 14px;
  font-weight: 400;
}
  ul, li{
    padding:0;
    margin:0;
    list-style-type:none;
  }
  .com-box{
    .com-title{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      .title-after{
        display: inline-block;
        width: 8px;
        height: 18px;
        background: #245399;
        border-radius: 10px 0 10px 0;
        margin-right: 2px;
        &:nth-child(2){
          opacity: 0.6;
        }
        &:nth-child(3){
          opacity: 0.2;
        }
      }
    }
  }
  .idm_shortcut_cont {
    font-size: 15px;
  }
  .summary-box{
    display: flex;
    // margin: 0 -5px;
    flex-wrap: wrap;
    .summary-item{
      padding: 5px;
      text-align: center;
      // margin-bottom: 10px;
    }
    .summary-bg{
      border-radius: 6px;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      // display: flex;
      // flex-direction: column;
      // justify-content: center;
      // align-items: flex-start;
      // padding-left: 5px;
    }
  }
