  padding:0;
  margin:0;
  list-style-type:none;
}
.com-box{
  .com-title{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    .title-after{
      display: inline-block;
      width: 8px;
      height: 18px;
      background: #245399;
      border-radius: 10px 0 10px 0;
      margin-right: 2px;
      &:nth-child(2){
        opacity: 0.6;
      }
      &:nth-child(3){
        opacity: 0.2;
      }
    }
  }
}
.idm_shortcut_cont {
  font-size: 15px;
  ::v-deep .van-grid-item__content{
    padding: 7px 3px;
  }
  /* 样式二 */
  .idm_applicationcenter_main_list{
    position: relative;
    text-align: center;
    .img_box{
      position: relative;
      // width: 40px;
      // height: 40px;
      margin: 0 auto 2.5px auto;
      img,svg{
        width: 100%;
        height: 100%;
      }
    }
    .number{
      width: 20px;
      height: 20px;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: -10px;
      right: -10px;
      text-align: center;
      font-size: 12px;
      overflow: hidden;
      color: white;
      background: #E81B1B;
      border-radius: 50%;
    }
  }
  /* 样式三 */
  .idm_applicationcenter_main_list-three {
    position: relative;
    .idm_applicationcenter_main_list_name {
      width: 100%;
      text-align: right;
      background-color: #ffffff;
      border-radius: 5px;
      padding: 0px 10px;
      display: flex;
      margin-top: 10px;
      .empty-view {
        width: 50px;
        height: 40px;
      }
      .tit {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
      }
    }
    // .img_box,img,svg{
    //     width: 40px;
    //     height: 40px;
    //     margin: 0 auto 2.5px auto;
    // }
    // .img_box {
    //     width: 40px;
    //     height: 40px;
    //     margin: 0 auto 2.5px auto;
    //     position: absolute;
    //     bottom: 10px;
    //     left: 10px;
    // }
    .img_box{
      // position: absolute;
      // bottom: 15px;
      // left: 5px;
      // width: 45px;
      // height: 50px;
      float: left;
      margin-bottom: -30px;
      margin-left: 10px;
      overflow: hidden;
      transform: rotate(120deg);
      img,svg{
        width: 100%;
        height: 100%;
      }
    }
    .two{
        width: 100%;
        height: 100%;
        overflow: hidden;
        transform: rotate(-60deg);
    }
    .three{
        width: 100%;
        height: 100%;
        overflow: hidden;
        transform: rotate(-60deg);
    }
    .number{
      width: 20px;
      height: 20px;
      position: absolute;
      top: 8px;
      display: flex;
      align-items: center;
      justify-content: center;
      right: -10px;
      text-align: center;
      font-size: 12px;
      overflow: hidden;
      color: white;
      background: #E81B1B;
      border-radius: 50%;
    }
  }
}
.short-box{
  display: flex;
  flex-wrap: wrap;
  // margin: 0 -5px;
  .short-item{
    position: relative;
    padding: 5px;
    text-align: center;
    display: flex;
    justify-content: center;
    // margin-bottom: 10px;
    .number {
      width: 20px;
      height: 20px;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: -3px;
      right: -3px;
      text-align: center;
      font-size: 12px;
      overflow: hidden;
      color: white;
      background: #E81B1B;
      border-radius: 50%;
    }
  }
  .short-bg{
    border-radius: 6px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
  }
}
