    padding: 7px 3px;
}
.van-popup{
    background-color: inherit;
}

.idm_filed_svg_icon {
    font-size: 14px;
    width: 14px;
    height: 14px;
    fill: currentColor;
    vertical-align: -0.15em;
    outline: none;
}
.idm_applicationcenter{
    .van-popup--center{
        width: 95% !important;
        height: 90vh !important;
        overflow-y: auto;
        padding: 40px 0 20px 0;
    }
    .application_search_pop{
        z-index: 10000;
    }
    .loading_box{
        padding: 20px 0;
        text-align: center;
    }
    .empty_block{
        padding: 10px 0;
        text-align: center;
        .van-empty__image{
            display: none;
        }
    }
}

