  background-color: #4A90E2FF;
  .top-bg{
    height: 100%;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center;
    color: #fff;
    padding: 10px 10px 20px 10px;
    display: flex;
    flex-direction: column;
    .top-set{
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    .top-content{
      flex: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 18px;
      margin: 5px;
      line-height: 1.5;
      .user-info{
        display: flex;
        align-items: center;
      }
      .default-logo{
        width: 36px;
        height: 36px;
        margin-right: 10px;
        border-radius: 50%;
        background: #ccc;
        display: inline-block;
      }
    }
  }
}
#top_setting_popup .ant-modal-wrap  {
  overflow: hidden;
  .ant-modal {
    width: 100%!important;
    height: 100%!important;
    max-width: initial;
    margin: 0!important;
    top:0;
    padding: 0;
    .ant-modal-content {
      background: transparent;
      height: 100%;
      box-shadow:none;
      border-radius: 0;
      .ant-modal-body {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
      }
    }
  }
} 
