  .hover_button {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #3976c7;
    position: fixed;
    z-index: 1000;
    top: 10px;
    background-color: #fff;
    .idm_filed_svg_icon {
      font-size: 16px;
      fill: currentColor;
      vertical-align: -0.15em;
      outline: none;
    }
  }
  .hover_button_left {
    padding: 0px 6px 0px 2px;
    border-radius: 0 50% 50% 0;
    left: 0;
  }
  .hover_button_right {
    padding: 0px 2px 0px 6px;
    border-radius: 50% 0 0 50%;
    right: 0;
  }
}
.idm_popupWorkbench_popup {
  width: 30%;
  height: 100%;
  .cell_box {
    background-color: #fff;
    &.cell_box_active {
      background-color: #d0e8ff;
    }
    .van-cell {
      background-color: transparent;
      :after {
        position: absolute;
        box-sizing: border-box;
        content: " ";
        pointer-events: none;
        right: 16px;
        bottom: 0;
        left: 16px;
        border-bottom: 1px solid #ebedf0;
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
      }
      &.cell_selected {
        color: #3976c7;
        background-color: #ebedf0;
      }
    }
  }
  .hover_button {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #3976c7;
    position: fixed;
    top: 10px;
    background-color: #fff;
    .idm_filed_svg_icon {
      font-size: 16px;
      fill: currentColor;
      vertical-align: -0.15em;
      outline: none;
    }
  }
  .hover_button_left {
    padding: 0px 6px 0px 2px;
    border-radius: 0 50% 50% 0;
    left: 100%;
  }
  .hover_button_right {
    padding: 0px 2px 0px 6px;
    border-radius: 50% 0 0 50%;
    right: 100%;
  }
}
