




































































































































































































































































































































































































































































































































































































































































































































































































































.idm-message-list-parent-box{
  overflow: hidden;
  position: relative;
  .idm-message-list-box{
    overflow: hidden;
    &-title{
      padding: 0 0 10px 0;
      color: #333;
      font-weight: 600;
      &-left-icon{
        display: flex;
        align-items: center;
        margin: 0 8px 0 0;
      }
      &-right-icon{
        display: flex;
        align-items: center;
      }
      &-icon{
        font-size: 14px;
        width: 14px;
        fill: currentColor;
        vertical-align: -0.15em;
        outline: none;
        margin: 0 0 0 8px;
      }
      &-right{
        font-weight: 400;
        color: #aaa;
        font-size: 14px;
      }
    }
    &-top{
      font: inherit;
      margin: 0 0 10px 0;
      &-left{
        white-space: nowrap;
        overflow: hidden;
        overflow-x: auto;
        scrollbar-width: none; /* firefox */
        -ms-overflow-style: none; /* IE 10+ */
        color: #888;
        &::-webkit-scrollbar {
          display: none;
        }
        >span{
          border-right: 1px solid #ccc;
          padding: 0 15px;
          &:first-child{
            padding: 0 15px 0 0;
          }
          &:last-child{
            padding: 0 0 0 15px;
            border: 0;
          }
        }
        .active{
          color: #333;
          font-weight: 600;
        }
      }
      &-more{
        font-size: 23px;
      }
    }
    &-top2{
      margin: 0 0 10px 0;
      font-size: 15px;
      &-left{
        white-space: nowrap;
        overflow: hidden;
        overflow-x: auto;
        scrollbar-width: none; /* firefox */
        -ms-overflow-style: none; /* IE 10+ */
        &::-webkit-scrollbar {
          display: none; 
        }
        >div{
          background-color: #eee;
          border-radius: 20px;
          overflow: hidden;
          margin: 0 4px;
          color: #000;
          padding: 2px 15px;
          display: flex;
          align-items: center;
          &:last-child{
            
          }
        }
        .active{
          color: rgb(61, 140, 243);
          font-weight: 500;
          background-color: transparent;
          font-weight: 600;
        }
      }
      &-more{
        font-size: 23px;
      }
    }
    &-list{
      padding: 0;
      list-style: none;
      > li{
        font-size: 16px;
        margin: 0 0 8px 0;
        &:last-child{
          margin:  0;
        }
      }
      &-style-square{
        color: #000;
        font-size: 10px;
        margin: 0 5px 0 0;
      }
      &-content{
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        white-space: nowrap;
        color: #000;
      }
      &-time{
        white-space: nowrap;
        color: #999;
        font-size: 14px;
        margin: 0 0 0 10px;
      }
    }
    &-list2{
      padding: 0;
      list-style: none;
      > li{
        
        width: 100%;
        margin: 0 0 10px 0;
        &:last-child{
          margin:  0;
        }
      }
      &-left-img{
        width: 80px;
        height: 70px;
        margin:0 10px 0 0;
        object-fit: fill;
      }
      &-left-img2{
        width: 60px;
        height: 60px;
        border-radius: 50%;
        margin:0 10px 0 0;
        object-fit: fill;
      }
      &-title{
        width: 100%;
        overflow:hidden;
        text-overflow:ellipsis;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:2;
        font-size: 15px;
        font-weight: 600;
        color: #888;
      }
      &-title2{
        width: 100%;
        overflow:hidden;
        text-overflow:ellipsis;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:1;
        font-size: 15px;
        font-weight: 600;
      }
      &-title-bottom{
        margin: 10px 0 0 0;
        color: #999;
        font-size: 14px;
      }
      &-title-bottom2{
        margin: 10px 0 0 0;
        color: #999;
        font-size: 3.7vw;
      }
    }
  }
  &-empty{
    overflow: hidden;
    height: 90px;
  }
  &-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0,0,0,.3);
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      padding: 6px 20px;
      color: #e6a23c;
      background: #fdf6ec;
      border:1px solid #f5dab1;
      border-radius: 4px;
    }
  }
}

