  background-color: #fff;
  overflow: hidden;
  position: relative;
  &-left-icon{
    display: flex;
    align-items: center;
    margin: 0 8px 0 0;
  }
  &-right-icon{
    display: flex;
    align-items: center;
    margin: 0 0 0 8px;
  }
  &-title{
    font: inherit;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-icon{
      width: 18px;
      height: 18px;
    }
    & &-more{
      font-size: 22px;
    }
    &-number{
      padding: 0 5px 0 0;
    }
    &-font {
      font-size: 16px;
      color: #333;
    }
  }
  &-overflow{
    overflow: hidden;
    overflow-y: auto;
  }
  &-sub{
    border-bottom: .6px solid #eee;
    &-title{
      display: flex;
      margin: 8px 0 0 0;
      &-icon{
        font-size: 15px;
        margin: 0 6px 0 0;
      }
    }
    &-content{
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      display: -moz-box;
      -moz-line-clamp: 2;
      -moz-box-orient: vertical;
      overflow-wrap: break-word;
      word-break: break-all;
      white-space: normal;
      overflow: hidden;
      letter-spacing: 1px;
      font-size: 15px;
    }
    &-no-read{
      color: #000;
      font-weight: 500;
    }
    &-hasRead{
      color: #999;
    }
    &-intr{
      padding: 8px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: #999;
      font-size: 15px;
    }
    &:last-child{
      border-bottom: 0
    }
    &-icon{
      font-size: 14px;
      width: 14px;
      fill: currentColor;
      vertical-align: -0.15em;
      outline: none;
      margin: 0 3px 0 0;
    }
    &-icon-no-read{
      font-size: 14px;
      width: 14px;
      fill: #f10215;
      vertical-align: -0.15em;
      outline: none;
      margin: 0 3px 0 0;
    }
    &-icon-has-read{
      font-size: 14px;
      width: 14px;
      fill: currentColor;
      vertical-align: -0.15em;
      outline: none;
      margin: 0 3px 0 0;
    }
    &-empty{
      overflow: hidden;
      height: 90px;
    }
  }
  &-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0,0,0,.3);
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      padding: 6px 20px;
      color: #e6a23c;
      background: #fdf6ec;
      border:1px solid #f5dab1;
      border-radius: 4px;
    }
  }
}
