.i-schedule-outer {
  width: auto;
  box-sizing: border-box;
  padding: 10px;
  font-family: PingFangSC-Regular;
  color: #333333;
  background-color: #fff;
  position: relative;
  font-size: calc(14px * #{ $scale });
  ul,
  li {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .i-schedule-header {
    display: flex;
    height: calc(40px * #{ $scale });
    line-height: calc(40px * #{ $scale });
    justify-content: space-between;

    .i-schedule-header-main {
      display: flex;
      .i-schedule-header-tit {
        font-family: PingFangSC-Regular;
        color: #333333;
        font-style: normal;
        text-decoration: none;
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: calc(16px * #{ $scale });

        span {
          margin: 0 5px;
        }
        
        transform: translateX(-5px);

        .idm_filed_svg_icon {
          font-size: calc(14px * #{ $scale });
          width: calc(14px * #{ $scale });
          fill: currentColor;
          vertical-align: -0.15em;
          outline: none;
        }
      }
      .i-schedule-header-date {
        margin-left: calc(10px * #{ $scale });
      }
    }

    .i-schedule-header-more {
      opacity: 0.5;
    }
  }

  .i-schedule-content {
    background-color: #fff;
    border-radius: calc(10px * #{ $scale });
    padding: calc(10px * #{ $scale }) calc(14px * #{ $scale });
    

    ::v-deep .van-loading {
      min-height: calc(130px * #{ $scale });
      justify-content: center;
    }

    .i-schedule-content-calendar.swiper-container {
      position: relative;
      width: 100%;
      overflow: hidden;
      .swiper-wrapper {
        height: calc(68px * #{ $scale });
        .swiper-slide {
          padding: calc(10px * #{ $scale });
          display: flex;
          flex-flow: row nowrap;
          justify-content: space-between;
          align-items: center;
          box-sizing: border-box;
          background-color: transparent;

          li {
            font-size: calc(14px * #{ $scale });
            text-align: center;
            width: calc(25px * #{ $scale });
            &.holiday {
              p {
                opacity: 0.5;
              }
              span {
                opacity: 0.5;
              }
              .active {
                opacity: 1;
              }
            }

            p {
              font-size: calc(14px * #{ $scale });
              opacity: 0.6;
            }

            span {
              margin-top: calc(5px * #{ $scale });
              display: inline-block;
            }

            .date-num.active {
              width: calc(18px * #{ $scale });
              height: calc(18px * #{ $scale });
              line-height: calc(18px * #{ $scale });
              text-align: center;
              background: #1890FF;
              color: #ffffff;
              border-radius: 50%;
              font-size: calc(14px * #{ $scale });
            }

            .today {
              display: inline-block;
              margin-top: calc(6px * #{ $scale });
              transform: scale(1.5);

              &.active {
                color: #1890FF;
              }
            }
          }
        }
      }
    }

    .i-schedule-content-note {
      border-top: calc(1px * #{ $scale }) solid #eee;
      padding-top: calc(14px * #{ $scale });
      height: calc(100% - calc(68px * #{ $scale }));
      overflow-y: auto;
      &::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 0px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 0px;
      }
      
      ::v-deep .ant-tabs {
        color: currentColor;
        font-size: inherit;

        .ant-tabs-bar {
          display: none;
        }
      }
      
      .i-schedule-content-note-inner {
        display: flex;

        .i-schedule-content-note-left {
          flex: 4;

          p {
            position: relative;
            height: calc(100px * #{ $scale });

            &.active::before {
              content: "";
              width: calc(5px * #{ $scale });
              height: calc(5px * #{ $scale });
              border-radius: 50%;
              background-color: #1890FF;
              position: absolute;
              top: 0;
              right: 0;
              transform: translateX(50%);
            }
          }
        }

        .i-schedule-content-note-right {
          flex: 10;
          border-left: calc(1px * #{ $scale }) solid #eee;
          padding-left: calc(14px * #{ $scale });
          overflow: hidden;

          .schedule-item {
            position: relative;
            height: calc(100px * #{ $scale });
            padding: 0 calc(10px * #{ $scale }) calc(10px * #{ $scale });

            & > div {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }

            .schedule-item-name {
              padding-bottom: calc(8px * #{ $scale });
            }
            .schedule-item-addr {
              opacity: .8;
            }
            .schedule-item-time {
              opacity: .6;
            }
          }
        }
      }
    }
  }

  .i-schedule-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0,0,0,.3);
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      padding: calc(6px * #{ $scale }) calc(20px * #{ $scale });;
      color: #e6a23c;
      background: #fdf6ec;
      border:calc(1px * #{ $scale }) solid #f5dab1;
      border-radius: calc(4px * #{ $scale });;
    }
  }

  ::v-deep .van-empty {
    padding: 0;
  }

  ::v-deep .van-empty__description {
    font-size: inherit;
  }
}
